::-webkit-scrollbar {
  display: none;
}

.center-header {
  z-index: 1;
}

::selection {
  background-color: #357fef;
  background-color: var(--primary);
  color: #fff;
}

body {
  font-family: "Poppins", sans-serif;
  position: relative;
  font-size: 14px;
  color: #4e4e4e;
  color: var(--secondary);
  overflow-x: hidden;
}
body.event {
  font-family: "Work Sans", sans-serif;
}

h1,
h2 {
  font-family: "Capriola", sans-serif;
  text-transform: capitalize;
}

h4,
h5,
li {
  margin-top: 0;
  margin-bottom: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  text-transform: capitalize;
}

h2,
h3,
h4,
h5,
h6,
li,
a {
  font-family: "Poppins", sans-serif;
  font-weight: 500;
  line-height: normal;
}

h2,
h3,
h4 {
  color: #357fef;
  color: var(--primary);
}

h5,
h6,
p {
  color: #4e4e4e;
  color: var(--secondary);
}

h1 {
  font-size: calc(22px + 28 * (100vw - 300px) / 1620);
  color: #fff;
  font-weight: 400;
  letter-spacing: 2px;
  margin-top: -7px;
  margin-bottom: -20px;
  line-height: 1.4;
}
h1 span.small-text {
  font-size: calc(18px + 22 * (100vw - 300px) / 1620);
  color: #fff;
}
h1 span.bold-text {
  font-weight: 600;
}
h1 span.theme-color {
  color: #357fef;
  color: var(--primary);
}

h2 {
  font-size: calc(20px + 16 * (100vw - 300px) / 1620);
  line-height: 0.9;
}

h3 {
  font-size: calc(18px + 8 * (100vw - 300px) / 1620);
  margin-top: -4px;
  margin-bottom: 0;
}

h4 {
  font-size: calc(16px + 4 * (100vw - 300px) / 1620);
}

h5 {
  font-size: calc(14px + 2 * (100vw - 300px) / 1620);
}

h6 {
  font-size: calc(13px + 1 * (100vw - 300px) / 1620);
  margin-top: -4px;
  margin-bottom: -5px;
}

p {
  font-size: calc(13px + 1 * (100vw - 300px) / 1620);
  color: #4e4e4e;
  color: var(--secondary);
  font-weight: 400;
  margin-top: -7px;
  margin-bottom: -4px;
  line-height: 1.8;
}

a {
  font-size: calc(13px + 1 * (100vw - 300px) / 1620);
  color: #4e4e4e;
  color: var(--secondary);
}
a:hover {
  text-decoration: none;
  cursor: pointer;
}
a:focus {
  outline: none;
}

.form-control {
  height: auto;
}

ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}
ul li {
  font-size: 14px;
}

.custom-scroll ::-webkit-scrollbar {
  width: 8px;
  background-color: #e3e3e3;
  border-radius: 25px;
}
.custom-scroll ::-webkit-scrollbar-thumb {
  background-image: linear-gradient(rgba(#13B8EA, 0.92) 0%, #4E56F3 100%);
  background-image: linear-gradient(rgba(var(--light), 0.92) 0%, var(--dark) 100%);
  border-radius: 25px;
}

.lable-nav {
  background-color: #fff;
  padding: 3px 9px;
  display: inline-block;
  position: absolute;
  top: -4px;
  letter-spacing: 1px;
  font-size: 10px;
  margin-left: 0;
  color: #4e4e4e;
  color: var(--secondary);
  line-height: 1;
  text-transform: capitalize;
}
.lable-nav:before {
  content: "";
  position: absolute;
  height: 5px;
  width: 5px;
  background-color: #fff;
  z-index: -1;
  top: 14px;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  left: 8px;
}

@-webkit-keyframes scaleDisappear {
  0% {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  60% {
    opacity: 0;
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
  }
  100% {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes scaleDisappear {
  0% {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  60% {
    opacity: 0;
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
  }
  100% {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
@-webkit-keyframes zoomIn {
  0% {
    opacity: 0;
    -webkit-transform: scale3d(0.3, 0.3, 0.3);
            transform: scale3d(0.3, 0.3, 0.3);
  }
  50% {
    opacity: 1;
  }
}
@keyframes zoomIn {
  0% {
    opacity: 0;
    -webkit-transform: scale3d(0.3, 0.3, 0.3);
            transform: scale3d(0.3, 0.3, 0.3);
  }
  50% {
    opacity: 1;
  }
}
@-webkit-keyframes slideInUp {
  0% {
    -webkit-transform: translate3d(0, 100%, 0);
            transform: translate3d(0, 100%, 0);
    visibility: visible;
  }
  to {
    -webkit-transform: translateZ(0);
            transform: translateZ(0);
  }
}
@keyframes slideInUp {
  0% {
    -webkit-transform: translate3d(0, 100%, 0);
            transform: translate3d(0, 100%, 0);
    visibility: visible;
  }
  to {
    -webkit-transform: translateZ(0);
            transform: translateZ(0);
  }
}
@-webkit-keyframes rotateInDownRight {
  0% {
    -webkit-transform-origin: right bottom;
            transform-origin: right bottom;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    opacity: 0;
  }
  to {
    -webkit-transform-origin: right bottom;
            transform-origin: right bottom;
    -webkit-transform: none;
            transform: none;
    opacity: 1;
  }
}
@keyframes rotateInDownRight {
  0% {
    -webkit-transform-origin: right bottom;
            transform-origin: right bottom;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    opacity: 0;
  }
  to {
    -webkit-transform-origin: right bottom;
            transform-origin: right bottom;
    -webkit-transform: none;
            transform: none;
    opacity: 1;
  }
}
.abs-center {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
