    -webkit-transform: scale(3.5);
            transform: scale(3.5);
  }
  100% {
    -webkit-transform: scale(5.5);
            transform: scale(5.5);
  }
}
@-webkit-keyframes ripple3 {
  0% {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
  }
  100% {
    -webkit-transform: scale(3.5);
            transform: scale(3.5);
